import {
	getJobFocus,
} from '@/api/job'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
export default {
	data() {
		return {
			ready: false,
			list: null,
			count: null,
			search: {
				page: 1,
				limit: 10
			},
			showlist: false
		}
	},
	onReady() {
		this.ready = true
	},
	onShow() {
		this.showlist = true
		this.getJobFocus();
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	methods: {
		getJobFocus() {
			const s = this.search
			const t = this
			let sl = this.showlist
			const search = Object.assign({}, {
				page: sl ? 1 : s.page,
				limit: sl ? 10 * s.page : s.limit,
			}, );
			getJobFocus(search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.count = r.list.count
					if (t.list && t.list.length > 0 && !sl) {
						t.list = t.list.concat(r.list.rows)
					} else {
						sl = false
						t.list = r.list.rows
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		detailTo(id) {
			uni.navigateTo({
				url: '/pages/company/detail?id=' + id
			});
		},
		jobTo() {
			uni.switchTab({
				url: '/pages/company/index'
			});
		},
		refresher() {
			let t = this
			if (t.count > t.list.length) {
				t.search.page = t.search.page++
				this.getJobFocus()
			} else {
				$wuxToast().hide()
				$wuxToast().show({
					type: 'forbidden',
					text: '已加载全部'
				})
			}
		}
	}
}
